import React from "react";
import { useNavigate } from "react-router-dom";
import arrow from "./arrow.png";

export default function AboutUs() {

    const navigate = useNavigate();

    return (
        <div className="h-screen grid grid-rows-6 grid-cols-6 bg-gradient-to-l from-indigo-200 via-red-200 to-yellow-100">
            <div className="row-start-1 row-span-1 col-start-1 col-span-1 flex justify-center items-center">
                <button className="
                    flex justify-center items-center
                    h-[11vh] w-[5.5vw] border-[#E5BA73] border-2 bg-[#FFFBE9]
                    hover:bg-[#C58940] hover:scale-[1.1] 
                    transition ease-in-out duration-150"
                    onClick={() => navigate("/")}
                >
                    <img src={arrow} className="scale-[0.35]"/>
                </button>
            </div>
            <div className="row-start-2 row-span-4 col-start-2 col-span-4 flex justify-center items-center font-['consolas'] text-[22.5px] text-justify">
            Web ca chng ti c nt tng ng vi phn mm Google Classroom. Nhng Classroom ch hin th bi tp di dng lit k, nn hc sinh kh nm bt c bi tp mn no, np  thi im no khi s lng bi tp ln, v t nht l lun qun hon thnh bi tp. Web ca chng ti  y s s dng Google Calendar API  hin th bi tp di dng lch  bn, 
            cho hc sinh mt ci nhn tng qut hn v nhng bi tp trong ngy, trong tun.
            Web cng s c ng h bo thc  nhc hc sinh lm bi. ng h s c t bi gio vin v trc khi tr hn np th ng h s thng bo n my hc sinh.
            </div>
        </div>
    );
}